<template>
    <div id="upper">
    <h1>테이블 정보 입력</h1>
        작성자 : <input type="text" placeholder="작성자를 입력해주세요" v-model="writer" id="writer">
        글 제목 : <input type="text" placeholder="글 제목을 입력해주세요" v-model="title" id="title">
        글 내용 : <input type="text" placeholder="글 내용을 입력해주세요" v-model="content" id="content">
        <button @click="inputData">입력하기</button>
    </div>
    <br>
    <hr>
    <br>
    <div id=data>
        <table id="table">
            <tr>
                <td>작성자</td><td>제목</td><td>내용</td><td>작성시간</td><td>조회수</td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                writer:'',
                title:'',
                content:'',
                regtime:'',
                hits:''
            };
        },
        methods:{
            inputData(){
                this.writer = document.getElementById('writer').value;
                this.title = document.getElementById('title').value;
                this.content = document.getElementById('content').value;
                this.regtime = new Date().toISOString().substring(0,11);
                this.hits = 0;

                const table = document.getElementById('table');
                const tr = document.createElement('tr');
                const result = table.appendChild(tr);

                result.appendChild(document.createElement('td')).innerHTML = this.writer;
                result.appendChild(document.createElement('td')).innerHTML = this.title;
                result.appendChild(document.createElement('td')).innerHTML = this.content;
                result.appendChild(document.createElement('td')).innerHTML = this.regtime;
                result.appendChild(document.createElement('td')).innerHTML = this.hits;

                this.writer = '';
                this.title = '';
                this.content = '';
                this.regtime = '';
                this.hits = '';
            }
        }
    }
</script>

<style>
    *{
        margin:0;
        padding:0;
    }

    #upper {
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }

    #upper button{
        color: darkblue;
    }
</style>